/* eslint-disable */
import WeightRecord from '@/objects/WeightRecord'
import StorageService from '@/services/StorageService'

function groupBy (array, f) {
  var groups = {}
  array.forEach((o) => {
    var group = JSON.stringify(f(o))
    groups[group] = groups[group] || []
    groups[group].push(o)
  })
  return Object.keys(groups).map((group) => {
    return groups[group]
  })
}

/* eslint no-extend-native: ["error", { "exceptions": ["Object"] }] */
Array.prototype.Average = function (prop) {
  var total = 0
  if (this.length < 2) {
    return this[0][prop]
  }
  for (var i = 0, _len = this.length; i < _len; i++) {
    total += parseFloat(this[i][prop])
  }
  return total / (i)
}

export default class WeightService {
  constructor (data) {
    this.dataLoaded = false
    this.storageService = new StorageService()
    this.weightData = []
    if (data) {
      this.weightData = data.sort((a, b) => {
        return new Date(b.date) - new Date(a.date)
      }).map((item) => {
        return new WeightRecord(item.date, item.value)
      })
      this.dataLoaded = true
    }
  }

  loadData () {
    this.weightData.length = 0
    var self = this
    return this.storageService.getAllWeights((value, key, index) => {
      if (value) {
        self.weightData.push(new WeightRecord(new Date(Date.parse(key)), value))
      }
    })
  }

  addWeight (date, weight) {
    this.storageService.addWeight(date, weight)
  }

  renderChart () {
    var monthNames = ['January', 'February', 'March', 'April', 'May', 'June',
      'July', 'August', 'September', 'October', 'November', 'December'
    ]
    var chartData = {
      labels: this.weightData.map((x) => {
        return `${monthNames[x.date.getMonth()]} ${x.date.getFullYear()}`
      }),
      datasets: [{
        label: 'Weight',
        backgroundColor: [
          '#448AFF'
        ],
        hoverBackgroundColor: [
          '#448AFF'
        ],
        data: this.weightData.map((x) => {
          return x.weight
        })
      }]
    }
    return chartData
  }

  renderAvgChart () {
    var monthNames = ['January', 'February', 'March', 'April', 'May', 'June',
      'July', 'August', 'September', 'October', 'November', 'December'
    ]

    var avgData = groupBy(this.weightData, (item) => {
      return [item.date.getMonth()]
    })
    var chartData = {
      labels: avgData.map((x) => {
        return `${monthNames[x[0].date.getMonth()]} ${x[0].date.getFullYear()}`
      }),
      datasets: [{
        label: 'Weight',
        backgroundColor: [
          '#448AFF'
        ],
        hoverBackgroundColor: [
          '#448AFF'
        ],
        data: avgData.map((x) => {
          return x.Average('weight')
        })
      }]
    }
    return chartData
  }
}



