<template>
<div class="">
  <md-subheader>DASHBOARD</md-subheader>
  <div class="md-layout md-gutter">
    <md-card class="md-layout-item md-size-49">
      <md-card-header>
        <div class="md-title">Your BMR (Base Metabolic Rate)</div>
      </md-card-header>

      <md-card-content>
        <h2>{{ user.BMR }}</h2>
      </md-card-content>
    </md-card>
    <md-card class="md-layout-item md-size-49">
      <md-card-header>
        <div class="md-title">Your TDEE (Total Daily Energy Expenediture)</div>
      </md-card-header>
      <md-card-content>
        <h2>{{ user.TDEE }}</h2>
      </md-card-content>
    </md-card>
  </div>
  <br />
  <br />
  <!-- FAT LOSS -->
  <div class="" v-if="fatLossChart">
    <IntakeWidget :model="fatLossChart" :service="fatLossMacroService" :title="'Fat Loss'" :calories="user.TDEE - 500"></IntakeWidget>
  </div>
  <div class="" v-if="maintenenceChart">
    <IntakeWidget :model="maintenenceChart" :service="maintenenceMacroService" :title="'Maintenence'" :calories="user.TDEE"></IntakeWidget>
  </div>
  <div class="" v-if="massChart">
    <IntakeWidget :model="massChart" :service="massMacroService" :title="'Mass'" :calories="user.TDEE + 500"></IntakeWidget>
  </div>
</div>
</template>

<script>
import router from '@/router'
import UserService from '@/services/UserService'
import MacroService from '@/services/MacroService'
import IntakeWidget from './widgets/IntakeWidget'
export default {
  components: {
    IntakeWidget
  },
  name: 'Home',
  data: () => ({
    user: new UserService(),
    fatLossChart: null,
    maintenenceChart: null,
    massChart: null,
    fatLossMacroService: null,
    maintenenceMacroService: null,
    massMacroService: null
  }),
  created: function () {
    this.user.GetUser().then((value) => {
      if (value != null) {
        var data = JSON.parse(value)
        this.user.name = data.name
        this.user.height = data.height
        this.user.weight = data.weight
        this.user.age = data.age
        this.user.isMale = data.isMale
        this.user.email = data.email
        this.user.activityLevel = data.activityLevel
        this.fatLossMacroService = new MacroService(this.user.TDEE - 500, 30, 50, 20)
        this.fatLossChart = this.fatLossMacroService.renderChart()
        this.maintenenceMacroService = new MacroService(this.user.TDEE, 30, 50, 20)
        this.maintenenceChart = this.maintenenceMacroService.renderChart()
        this.massMacroService = new MacroService(this.user.TDEE + 500, 30, 50, 20)
        this.massChart = this.massMacroService.renderChart()
      } else {
        router.push('register')
      }
    }).then((err) => {
      if (err) {
        console.log('error: ' + err)
        router.push('register')
      }
    })
  },
  methods: {

  }
}
</script>

<style>

</style>



