<template>
  <div class="page-container">
    <md-app md-waterfall md-mode="overlap" style="height:100vh;">

      <md-app-toolbar class="md-primary md-large">
        <div class="md-toolbar-row">
          <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
            <md-icon>menu</md-icon>
          </md-button>

          <span class="md-title">Calorie Calculator</span>
        </div>
      </md-app-toolbar>

      <md-app-drawer :md-active.sync="menuVisible">
        <md-toolbar class="md-transparent" md-elevation="0">
          Navigation
        </md-toolbar>

        <md-list>
          <md-list-item v-on:click="menuVisible = false" href="/#/">
            <md-icon>home</md-icon>
            <span class="md-list-item-text">Home</span>
          </md-list-item>

          <md-list-item v-on:click="menuVisible = false" href="/#/Register">
            <md-icon>person</md-icon>
            <span class="md-list-item-text">Account</span>
          </md-list-item>

          <md-list-item v-on:click="menuVisible = false" href="/#/Weight">
            <md-icon>linear_scale</md-icon>
            <span class="md-list-item-text">Weight Tracker</span>
          </md-list-item>

          <md-list-item v-on:click="showDeleteUserDialog = true" href="#">
            <md-icon>delete</md-icon>
            <span class="md-list-item-text">Delete User</span>
          </md-list-item>
        </md-list>
      </md-app-drawer>

      <md-app-content>
          <router-view></router-view>
      </md-app-content>
    </md-app>
    <md-snackbar :md-active.sync="delSaved">{{delMsg}}</md-snackbar>
    <md-dialog-confirm
        :md-active.sync="showDeleteUserDialog"
        md-title="Are you sure you wish to delete all your data?"
        md-content="This action is irreversible"
        md-confirm-text="Yes"
        md-cancel-text="No"
        @md-cancel="onCancel"
        @md-confirm="onConfirm" />
    </div>
  </div>
</template>
<script>
import StorageInterfacer from '@/services/StorageService'
import router from '@/router'

export default {
  name: 'app',
  data: () => ({
    menuVisible: false,
    showDeleteUserDialog: false,
    storageService: null,
    delSaved: false,
    delMsg: ''
  }),
  created: function () {
    this.storageService = new StorageInterfacer()
  },
  methods: {
    onCancel () {
      this.showDeleteUserDialog = false
    },
    onConfirm () {
      this.showDeleteUserDialog = false
      console.log(this.storageService)
      this.storageService.formatData().then(() => {
        this.delSaved = true
        this.delMsg = 'User deleted successfuly'
        router.push('register')
      }).catch((err) => {
        this.delSaved = true
        this.delMsg = err
      })
    }
  }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  height: 100vh;
}

header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}
</style>



