// import User from 'UserService'
import localforage from 'localforage'
/*eslint-disable */
// eslint-disable-next-line no-use-before-define
import WeightRecord from '@/objects/WeightRecord'

var userKey = 'userKey'

var userStore = localforage.createInstance({
  name: 'userStore'
})

var weightStore = localforage.createInstance({
  name: 'weightStore'
})

export default class StorageInterfacer {
  setUser (User) {
    userStore.setItem(userKey, JSON.stringify(User)).then(function () {
      return userStore.getItem(userKey)
    }).then(function (value) {
      console.log(value)
    }).catch(function (err) {
      console.log(err)
    })
  }
  getUser () {
    return userStore.getItem(userKey)
  }

  addWeight (date, weight) {
    weightStore.setItem(date, weight).then(() => {
      return weightStore.getItem(date)
    }).then((value) => {
      console.log(value)
    }).catch((err) => {
      console.log(err)
    })
  }

  getAllWeights (callback) {
    return weightStore.iterate(callback)
  }

  formatData () {
    weightStore.clear()
    return userStore.clear()
    // userStore.clear().then(() => {
    //   return weightStore.clear()
    // }).catch((err) => {
    //   return new Promise((resolve, reject) => {
    //     resolve(err)
    //   })
    // })
  }
}



