<template>
  <md-card class="space-below">
    <md-card-header>
      <div class="md-display-1">{{title}}</div>
    </md-card-header>
    <md-card-content class="md-layout md-gutter">
    <div class="md-layout-item md-size-49">
      <pie-chart :height="150" :chart-data="model"></pie-chart>
    </div>
    <div class="md-layout-item md-size-49">
      <h2 class="text-center">Calorie Breakdown</h2>
      <ul class="calorie-breakdown">
        <li>Calorie Intake: <strong>{{ calories }}</strong></li>
        <li>Protein Intake: <strong>{{ service.protein }}g</strong></li>
        <li>Carbs Intake: <strong>{{ service.carbs }}g</strong></li>
        <li>Fat Intake: <strong>{{ service.fats }}g</strong></li>
      </ul>
    </div>
    </md-card-content>
  </md-card>
</template>
<script>
import PieChart from '@/components/charts/PieChart.js'
export default {
  components: {
    PieChart
  },
  props: {
    model: {
      type: Object,
      required: true
    },
    calories: {
      type: Number,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    service: {
      type: Object,
      required: true
    }
  },
  name: 'IntakeWidget',
  data: () => ({

  }),
  methods: {

  }
}
</script>
<style>
  .text-center{
    text-align: center;
  }
  .calorie-breakdown{
    list-style: none;
    padding:0;
  }
  .space-below{
    margin-bottom: 20px;
  }
</style>



