<template>
<div class="">
  <md-subheader>Weight Tracker</md-subheader>

  <form novalidate class="md-layout" @submit.prevent="validateWeight">
    <md-card class="md-layout-item md-size-100 md-small-size-100 longcard">
      <md-card-header>
        <div class="md-title">Add some weight</div>
      </md-card-header>

      <md-card-content>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('weight')">
              <label for="first-name">Weight</label>
              <md-input name="weight" id="weight" v-model="form.weight" :disabled="sending" />
              <span class="md-error" v-if="!$v.form.weight.required">The weight is required</span>
              <span class="md-error" v-else-if="!$v.form.weight.minlength">Invalid weight</span>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100">
              <md-datepicker name="date" id="date" :disabled="sending" v-model="form.date">
              </md-datepicker>
          </div>
        </div>
      </md-card-content>

      <md-progress-bar md-mode="indeterminate" v-if="sending" />

      <md-card-actions>
        <md-button type="submit" class="md-primary" :disabled="sending">Add Weight</md-button>
      </md-card-actions>
    </md-card>
      <WeightWidget v-if="chartData" :model="chartData"></WeightWidget>
    <md-snackbar :md-active.sync="weightSaved">Success</md-snackbar>
  </form>
</div>
</template>

<script>
import router from '@/router'
import {
  validationMixin
} from 'vuelidate'
import {
  required,
  minLength,
  maxLength
} from 'vuelidate/lib/validators'
import WeightWidget from '@/components/widgets/WeightWidget'
import WeightService from '@/services/WeightService'
import WeightRecord from '@/objects/WeightRecord'
import UserService from '@/services/UserService'

export default {
  name: 'Weight',
  components: {
    WeightWidget
  },
  mixins: [validationMixin],
  data: () => ({
    form: {
      date: new Date(),
      weight: null
    },
    sending: false,
    weightSaved: false,
    weightData: null,
    weightService: null,
    chartData: null,
    user: null
  }),
  created: function () {
    this.user = new UserService()
    this.user.GetUser().then((value) => {
      if (value != null) {
        var data = JSON.parse(value)
        this.form.weight = data.weight
      }
    }).then((err) => {
      if (err) {
        console.log('error: ' + err)
      }
    })
    this.weightService = new WeightService()
    this.loadChartData()
  },
  validations: {
    form: {
      weight: {
        required,
        minLength: minLength(1),
        maxLength: maxLength(3)
      },
      date: {
        required
      }
    }
  },
  methods: {
    onConfirm () {
      router.push('/')
    },
    onCancel () {
    },
    loadChartData () {
      this.weightService.loadData().then(() => {
        this.weightService.weightData = this.weightService.weightData.sort((a, b) => {
          return new Date(a.date) - new Date(b.date)
        })
        this.chartData = this.weightService.renderAvgChart()
      }).catch((err) => {
        console.log(err)
      })
    },
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]

      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    clearForm () {
      this.$v.$reset()
      this.form.date = null
      this.form.weight = null
    },
    addWeight () {
      console.log('adding weight')
      console.log(this.form.date)
      this.weightService.addWeight(this.form.date, this.form.weight)
      this.sending = true

      // Instead of this timeout, here you can call your API
      this.weightSaved = true
      this.sending = false
      this.weightService.weightData.push(new WeightRecord(this.form.date, this.form.weight))
      this.weightService.weightData = this.weightService.weightData.sort((a, b) => {
        return new Date(a.date) - new Date(b.date)
      })
      this.chartData = this.weightService.renderAvgChart()
      // this.clearForm()
    },
    validateWeight () {
      this.$v.$touch()

      if (!this.$v.$invalid) {
        this.addWeight()
      }
    }
  }
}
</script>

<style>
/*.md-card{
  min-height: 60vh;
}*/
.md-menu-content{
    max-width: 500px !important;
}
.longcard {
  overflow: hidden !important;
  padding-bottom: 50px;
}
</style>



