import StorageInterfacer from './StorageService'

var storageService = new StorageInterfacer()

export default class User {
  constructor (form) {
    if (form) {
      this.name = form.name
      this.height = form.height
      this.weight = form.weight
      this.age = form.age
      this.isMale = form.gender === 'M'
      this.activityLevel = form.activityLevel
      this.email = form.email
    } else {
      this.name = null
      this.height = null
      this.weight = null
      this.age = null
      this.isMale = null
      this.activityLevel = null
      this.email = null
    }
  }

  get BMR () {
    if (this.IsEmpty) {
      return null
    }
    if (this.isMale) {
      return parseInt(66 + (13.7 * parseFloat(this.weight)) + (5.0 * parseFloat(this.height)) - (6.8 * parseFloat(this.age)))
    } else {
      return parseInt(655 + (9.6 * this.weight) + (1.8 * this.height) - (4.7 * this.age))
    }
  }

  get TDEE () {
    return parseInt(this.BMR * this.activityLevel)
  }

  get IsEmpty () {
    return this.name == null || this.height == null || this.weight == null || this.age == null || this.isMale == null || this.activityLevel == null || this.email == null
  }

  SaveUser () {
    storageService.setUser(this)
  }

  GetUser () {
    return storageService.getUser()
  }
}



