<template>
<div class="">
  <div class="">
    <md-dialog-confirm
        :md-active.sync="showExistingUserDialog"
        md-title="Looks like you've already registered"
        :md-content="showExistingUserDialog_msg"
        md-confirm-text="Yes"
        md-cancel-text="Edit"
        @md-cancel="onCancel"
        @md-confirm="onConfirm" />
    </div>
  <form novalidate class="md-layout" @submit.prevent="validateUser">
    <md-card class="md-layout-item md-size-100 md-small-size-100 longcard">
      <md-card-header>
        <div class="md-title">Let's calculate your BMR and TDEE to findout your dietary needs.</div>
      </md-card-header>

      <md-card-content>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('name')">
              <label for="first-name">Name</label>
              <md-input name="name" id="name" autocomplete="given-name" v-model="form.name" :disabled="sending" />
              <span class="md-error" v-if="!$v.form.name.required">The name is required</span>
              <span class="md-error" v-else-if="!$v.form.name.minlength">Invalid name</span>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('activityLevel')">
              <label for="gender">Activity Level</label>
              <md-select name="activityLevel" id="activityLevel" v-model="form.activityLevel" md-dense :disabled="sending">
                <md-option value="1.2">Sedentary – Little to no Exercise/desk job</md-option>
                <md-option value="1.375">Lightly active – Light exercise/sports 1 – 3 days a week</md-option>
                <md-option value="1.55">Moderately active – Moderate Exercise/sports 3 – 5 days a week</md-option>
                <md-option value="1.725">Very active – Heavy Exercise/sports 6 – 7 days a week</md-option>
                <md-option value="1.9">Extremely active – Very heavy exercise/physical job/sports 2x day</md-option>
              </md-select>
              <span class="md-error">The activity level is required</span>
            </md-field>
          </div>
        </div>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('weight')">
              <label for="first-name">Weight</label>
              <md-input name="weight" id="weight" v-model="form.weight" :disabled="sending" />
              <span class="md-error" v-if="!$v.form.weight.required">The weight is required</span>
              <span class="md-error" v-else-if="!$v.form.weight.minlength">Invalid weight</span>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('height')">
              <label for="last-name">Height</label>
              <md-input name="height" id="height" v-model="form.height" :disabled="sending" />
              <span class="md-error" v-if="!$v.form.height.required">The height is required</span>
              <span class="md-error" v-else-if="!$v.form.height.minlength">Invalid height</span>
            </md-field>
          </div>
        </div>
        <div class="md-layout md-gutter">
          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('gender')">
              <label for="gender">Gender</label>
              <md-select name="gender" id="gender" v-model="form.gender" md-dense :disabled="sending">
                <md-option value="M">Male</md-option>
                <md-option value="F">Female</md-option>
              </md-select>
              <span class="md-error">The gender is required</span>
            </md-field>
          </div>

          <div class="md-layout-item md-small-size-100">
            <md-field :class="getValidationClass('age')">
              <label for="age">Age</label>
              <md-input type="number" id="age" name="age" autocomplete="age" v-model="form.age" :disabled="sending" />
              <span class="md-error" v-if="!$v.form.age.required">The age is required</span>
              <span class="md-error" v-else-if="!$v.form.age.maxlength">Invalid age</span>
            </md-field>
          </div>
        </div>

        <md-field :class="getValidationClass('email')">
          <label for="email">Email</label>
          <md-input type="email" name="email" id="email" autocomplete="email" v-model="form.email" :disabled="sending" />
          <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
          <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
        </md-field>
      </md-card-content>

      <md-progress-bar md-mode="indeterminate" v-if="sending" />

      <md-card-actions>
        <md-button type="submit" class="md-primary" :disabled="sending">{{ isEdit ? 'Save' : 'Register' }}</md-button>
      </md-card-actions>
    </md-card>

    <md-snackbar :md-active.sync="userSaved">The user {{ lastUser }} was saved with success!</md-snackbar>
  </form>
</div>
</template>

<script>
import UserService from '@/services/UserService'
import router from '@/router'
import {
  validationMixin
} from 'vuelidate'
import {
  required,
  email,
  minLength,
  maxLength
} from 'vuelidate/lib/validators'

export default {
  name: 'Register',
  mixins: [validationMixin],
  data: () => ({
    form: {
      name: null,
      gender: null,
      age: null,
      email: null,
      weight: null,
      height: null,
      activityLevel: null
    },
    userSaved: false,
    sending: false,
    lastUser: null,
    user: null,
    showExistingUserDialog: false,
    showExistingUserDialog_msg: '',
    isEdit: false
  }),
  created: function () {
    this.user = new UserService()
    this.user.GetUser().then((value) => {
      if (value != null) {
        var data = JSON.parse(value)
        this.form.name = data.name
        this.form.height = data.height
        this.form.weight = data.weight
        this.form.age = data.age
        this.form.gender = data.isMale ? 'M' : 'F'
        this.form.email = data.email
        this.form.activityLevel = data.activityLevel
        this.user = new UserService(this.form)
        this.isEdit = true
        this.showExistingUserDialog = true
        this.showExistingUserDialog_msg = `Hey ${this.form.name}, it seems you already registered.<br />Skip this step?`
      }
    }).then((err) => {
      if (err) {
        console.log('error: ' + err)
      }
    })
  },
  validations: {
    form: {
      name: {
        required,
        minLength: minLength(3)
      },
      activityLevel: {
        required
      },
      weight: {
        required,
        minLength: minLength(1)
      },
      height: {
        required
      },
      age: {
        required,
        maxLength: maxLength(3)
      },
      gender: {
        required
      },
      email: {
        required,
        email
      }
    }
  },
  methods: {
    onConfirm () {
      router.push('/')
    },
    onCancel () {
    },
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]

      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    clearForm () {
      this.$v.$reset()
      this.form.name = null
      this.form.age = null
      this.form.gender = null
      this.form.email = null
      this.form.weight = null
      this.form.height = null
    },
    saveUser () {
      this.sending = true
      var curUser = new UserService(this.form)

      curUser.SaveUser()
      // Instead of this timeout, here you can call your API
      this.lastUser = `${this.form.name}`
      this.userSaved = true
      this.sending = false
      this.clearForm()
      router.push('/')
    },
    validateUser () {
      this.$v.$touch()

      if (!this.$v.$invalid) {
        this.saveUser()
      }
    }
  }
}
</script>

<style>
/*.md-card{
  min-height: 60vh;
}*/
.md-menu-content{
    max-width: 500px !important;
}
</style>



