// 1g Protien = 4 Caloris
// 1g Carbohydrate = 4 Calories
// 1g Fat = 9 Calories

export default class Macro {
  constructor (cals, protein, carbs, fat) {
    if (protein + carbs + fat === 100) {
      this.calories = cals
      this.protein = parseInt(((cals / 100) * protein) / 4)
      this.carbs = parseInt(((cals / 100) * carbs) / 4)
      this.fats = parseInt(((cals / 100) * fat) / 9)
    }
  }

  renderChart () {
    return {
      labels: ['Protein', 'Carbs', 'Fat'],
      datasets: [{
        label: 'Macros',
        backgroundColor: [
          '#FF6384',
          '#36A2EB',
          '#FFCE56'
        ],
        hoverBackgroundColor: [
          '#FF6384',
          '#36A2EB',
          '#FFCE56'
        ],
        data: [this.protein, this.carbs, this.fats]
      }]
    }
  }
}



