<template>
  <div class="md-layout md-gutter">
  <div class="md-layout-item md-size-100">
    <line-chart :height="150" :chart-data="model"></line-chart>
  </div>
  </div>
</template>

<script>
import LineChart from '@/components/charts/LineChart.js'
export default {
  components: {
    LineChart
  },
  props: {
    model: {
      type: Object,
      required: true
    }
  },
  name: 'WeightWidget',
  data: () => ({

  })
}
</script>



